/*
 * stats
 */

.results-stats {
  &::after {
    @include pseudo($pos: static);
    background: url(#{$path}/images/results-stats.jpg) no-repeat 50% 100%;
    background-size: cover;
    height: 51vw;
    margin-left: unit(-1);
    margin-top: -14vw;
    position: relative;
    width: calc(100% + #{unit(2)});
    z-index: -1;

    @media (min-resolution: 120dpi) and (max-width: map-get($breakpoints, 'small--down')) {
      background-image: url(#{$path}/images/results-stats@2x.jpg);
    }

    @include respond-to('small') {
      background-image: url(#{$path}/images/results-stats--small.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'small')) {
      background-image: url(#{$path}/images/results-stats--small@2x.jpg);
    }

    @include respond-to('medium') {
      background-image: url(#{$path}/images/results-stats--medium.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium')) {
      background-image: url(#{$path}/images/results-stats--medium@2x.jpg);
    }

    @include respond-to('medium-large') {
      background-image: url(#{$path}/images/results-stats--medium-large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium-large')) {
      background-image: url(#{$path}/images/results-stats--medium-large@2x.jpg);
    }

    @include respond-to('large') {
      background-image: url(#{$path}/images/results-stats--large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'large')) {
      background-image: url(#{$path}/images/results-stats--large@2x.jpg);
    }

    @include respond-to('x-large') {
      background-image: url(#{$path}/images/results-stats--x-large.jpg);
    }

    @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'x-large')) {
      background-image: url(#{$path}/images/results-stats--x-large@2x.jpg);
    }

    @media (min-width: 107.5em) {
      height: 87.6rem;
      margin-top: -24rem;
    }
  }
}

/*
 * results
 */

.results-progress__list {
  @include reset-list;
}

.results-progress__list__item {
  @include null-margins;

  p {
    margin: unit(0.2) 0;
  }
}

.results-progress__item__measurement {
  font-size: 1.2rem;
  font-weight: $font-weight--bold;
}

.results-progress__sidebar {
  display: block;
  position: relative;

  img {
    height: auto;
    width: 100%;
  }
}
